// A mock function to mimic making an async request for data
import axios from "axios";
export function createProduct(data) {
  return new Promise(async (resolve) => {
    const response = await axios.post("/products", data);
    resolve(response);
  });
}
export function updateProduct(data) {
  return new Promise(async (resolve) => {
    const response = await axios.patch(`/products/${data.id}`, data);
    resolve(response);
  });
}
export function fetchProductByID(id) {
  return new Promise(async (resolve) => {
    const data = await axios.get(`/products/${id}`);
    // console.log(data);
    resolve(data);
  });
}
export function fetchBrands() {
  return new Promise(async (resolve) => {
    const data = await axios.get("/brands");
    resolve(data);
  });
}
export function fetchCategories() {
  return new Promise(async (resolve) => {
    const data = await axios.get("/categories");
    resolve(data);
  });
}
export function fetchProductsByFilters(filter, sort, pagenation) {
  //filter={"category":["smartphone"],
  //        "brand":["Apple"]      }
  //sort={"_sort":"price",
  //      "_order":"asc"}
  //pagenation={"_page":1,
  //      "_limit":10}
  let queryString = "";
  for (let key in filter) {
    if (Array.isArray(filter[key]) === 0) {
      queryString += `${key}=${filter[key]}&`;
    } else {
      filter[key].map((curr) => {
        queryString += `${key}=${curr}&`;
      });
    }
  }
  for (let key in sort) {
    queryString += `${key}=${sort[key]}&`;
  }
  for (let key in pagenation) {
    queryString += `${key}=${pagenation[key]}&`;
  }
  // console.log(queryString);

  return new Promise(async (resolve) => {
    const data = await axios.get(`/products?${queryString}`);
    resolve(data);
  });
}
